import React, {useState, KeyboardEvent} from 'react';
import './App.css';
import API from "./API";


type tempType = {

    feels_like: number;
    temp_min: number;
    name: string;
}
type weatherType = { main: string,  icon: string }



function App() {


    const [value, setValue] = useState('')
    const [weather, setWeather] = useState<weatherType>()
    const [temp, setTemp] = useState<tempType>({temp_min: 0, feels_like: 0, name: 'Enter citi'})
    const [error, setError] = useState('')
    const [img,setImg]=useState('data:image/png;base64,')
    const onKeyPressHandler = (e: KeyboardEvent<HTMLInputElement>) => {
        if (e.code === "Enter") {
            searchWeatherHandler(e.currentTarget.value)
            setValue('')
        }
    }

    const searchWeatherHandler = (title: string) => {
        API.searchWeatherByCiti(title)
            .then(({data}) => {
                console.log(data)
                const {main, weather, name} = data
                const {temp_min, feels_like} = main
                const temp = {temp_min, feels_like, name}
                setTemp(temp)
                setError('')
                return weather
            })
            .then((resp) => {
                    const {icon,main} = resp[0]
                    setWeather({icon,main})
                    if (icon === '01d'){
                        setImg("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQBLtPVHNeIipOAc238W5jjjFM9IFPjVbTscQ&usqp=CAU")
                    }
                    else if (icon==='04d'){
                        setImg("https://st4.depositphotos.com/38837296/39705/v/450/depositphotos_397057922-stock-illustration-weather-icon-vector-weather-concept.jpg")
                    }
                    else if (icon ==='03n'){
                        setImg("https://icon-library.com/images/partly-cloudy-weather-icon/partly-cloudy-weather-icon-24.jpg")
                    }
                    else{
                        API.searchImgBYIcon(icon)
                            .then(resp => {
                                const img = resp.request.responseURL
                                setImg(img)

                            })
                            .catch(rej => {
                                setError(rej)
                                setTemp({...temp,name:''})
                            })
                    }

            })
            .catch((rej) => {
                const {message} = rej
                setError(message)
                setTemp({...temp,name:''})
            })
    }
    const temperature = Math.floor(temp.temp_min)
    const temperatureFilsLike = Math.floor(temp.feels_like)
    return (
        <div className={`wrapper ${temp.temp_min <= 0 ? "cold" : "hot"}`}>
                <div className={"leftSide"}>
                    <div className={"temp"}>
                        <div>temperature now
                            <span>{temperature}</span></div>
                        <div>temperature fills like <span>{temperatureFilsLike}</span></div>
                    </div>
                </div>
                <div className={"rightSide"}>
                    <div className={"search"}>
                        <div><img alt={"weather"} className={"img"} src={img}/></div>
                        <div className={"fomSearch"}>
                            <input
                            value={value}
                            onKeyPress={onKeyPressHandler}
                            onChange={(e) => setValue(e.currentTarget.value)}/>
                            <button onClick={() => searchWeatherHandler(value)}>search</button>
                        </div>
                        <div className={"errors"}>
                            <div>{error}</div>
                            <span>{temp.name}</span></div>
                        </div>
                </div>
        </div>
    );
}

export default App;
